<template>
	<div id="single-blog">
		<h2>{{ blog.title }}</h2>
		<article>{{ blog.content }}</article>
		<p>作者: {{ blog.writer }}</p>
		<p>分类:</p>
		<ul>
			<li v-for = "job in blog.jobs">
				{{ job }}
			</li>
		</ul>
	</div>
</template>

<script>
	export default{
		name:'single-blog',
		data(){
			return{
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
			this.$http.get('https://vue-myblog-f337c.firebaseio.com/posts/' + this.id +'.json')
				.then(function(data){
					// console.log(data);
					//this.blog = data.body;
					return data.json();
				})
				.then(function(data){
					this.blog = data;
				})
		}

	}
</script>

<style>
	#single-blog{
		max-width: 960px;
		margin:0 auto;
		padding:20px;
		background: #eee;
		border:1px dotted #aaa;
	}
</style>